html {
  box-sizing: border-box;
  font-size: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  padding: 20px;
  background-color: #cecece;
  color: #000000;
  font-family: "Roboto", sans-serif;
  line-height: 1.3;
}
@media only screen and (min-width: 1000px) {
  body {
    padding: 1.5rem;
  }
}

.task_container {
  background-color: #ffffff;
}
.task_container.box_1 {
  width: 300px;
  height: 250px;
  border: 1px solid #cecece;
}
.task_container.box_1 .logo_container {
  width: 220px;
  margin: 0 auto;
}
.task_container.box_2 {
  display: grid;
  grid-template-rows: 1fr 70px;
  width: 160px;
  height: 600px;
  overflow: hidden;
}
.task_container.box_2 .logo_container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 70px;
  width: 140px;
  margin: 0 auto;
}
.task_container.box_3 {
  width: fit-content;
  padding: 10px 25px;
}
.task_container.box_3 .count_date {
  font-size: 24px;
  font-weight: 500;
  color: #ee4036;
}
.task_container.box_3 .count_date #counter {
  color: #432844;
}
.task_container .items_container {
  position: relative;
  padding: 5px;
}
.task_container .items_container .item {
  border: 1px solid #cecece;
  max-width: 150px;
  width: 100%;
  background-color: #ffffff;
}
.task_container .items_container .item.activeBorder {
  border: 1px solid #ee4036;
  outline: 1px solid #ee4036;
}
.task_container .items_container .item_img {
  display: block;
  width: 100%;
  height: initial;
  object-fit: contain;
  object-position: center;
}
.task_container .items_container .item_title, .task_container .items_container .item_price {
  display: block;
  text-align: center;
  height: fit-content;
  margin: auto;
  font-size: 12px;
}
.task_container .items_container .item_title {
  font-weight: 400;
}
.task_container .items_container .item_price {
  font-weight: 700;
}
.task_container .items_container .close {
  display: block;
  width: 100%;
  padding: 5px;
  text-align: right;
  color: #cecece;
  font-size: 12px;
  cursor: pointer;
}
.task_container .items_container .action_btn {
  display: block;
  margin: auto;
  background-color: #ee4036;
  color: #ffffff;
  border: unset;
  font-size: 16px;
  padding: 8px 10px;
  width: 110px;
  transition: background-color 0.1s ease-in-out;
  cursor: pointer;
}
.task_container .items_container .action_btn:hover {
  background-color: #de1e13;
}
.task_container .items_container#task_1 {
  width: 300px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 2px;
}
.task_container .items_container#task_1 .item_img {
  height: 70px;
  margin: 5px 0;
}
.task_container .items_container#task_2 .item {
  position: absolute;
  display: grid;
  grid-template-columns: 1;
  grid-template-rows: 25px 210px 110px 110px 65px;
  transition: transform 0.5s ease;
  transform: translateX(160px);
  z-index: 0;
}
.task_container .items_container#task_2 .item.activeSlider {
  transform: translateX(0);
  z-index: 2;
}
.task_container .items_container#task_2 .item_img {
  height: 210px;
}

.logo_container {
  padding: 5px;
}
.logo_container img {
  display: block;
  max-width: 100%;
  height: initial;
  object-fit: contain;
}